body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh; /* Ensures page fills viewport height */
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh; /* Inherits viewport height from body */
}

.header {
  background-color: #e3f2fc;
  background-image: linear-gradient(to bottom, #e3f2fc 0%, #e9f7ff 100%);
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header h1 {
  color: #fff;
  font-size: 24px;
  margin: 0;
}

.task-list {
  flex-grow: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.task-list h2 {
  font-size: 18px;
  margin: 0;
  padding-bottom: 5px;
  border-bottom: 1px solid #ddd;
}

.task-item {
  background-color: #fff;
  border: 1px solid #e3f2fc;
  border-radius: 4px;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.2s ease-in-out;
}

.task-item.completed {
  background-color: #f2f9ff;
  border-color: #ddd;
  text-decoration: line-through;
  color: #999;
}

.task-item span {
  font-size: 16px;
}

.task-item input[type="checkbox"] {
  margin-right: 10px;
}

.footer {
  background-color: #fff;
  border-top: 1px solid #ddd;
  padding: 10px;
  text-align: center;
  font-size: 12px;
}

/* Media queries for responsiveness */

@media only screen and (max-width: 768px) {
  .task-list {
    flex-direction: column; /* Stack sections vertically on smaller screens */
  }
  .task-item {
    flex-direction: column; /* Stack task content vertically on smaller screens */
  }
}

@media only screen and (max-width: 480px) {
  .header h1 {
    font-size: 20px; /* Reduce font size for better readability on very small screens */
  }
  .task-item span {
    font-size: 14px; /* Reduce font size for better readability on very small screens */
  }
}
